@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

/*helper classes*/
  html, body, div, span, applet, object, iframe,
  h1, h2, h3, h4, h5, h6, p, blockquote, pre,
  a, abbr, acronym, address, big, cite, code,
  del, dfn, em, img, ins, kbd, q, s, samp,
  small, strike, strong, sub, sup, tt, var,
  b, u, i, center,
  dl, dt, dd, ol, ul, li,
  fieldset, form, label, legend,
  table, caption, tbody, tfoot, thead, tr, th, td,
  article, aside, canvas, details, embed, 
  figure, figcaption, footer, header, hgroup, 
  menu, nav, output, ruby, section, summary,
  time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
  }
  /* HTML5 display-role reset for older browsers */
  article, aside, details, figcaption, figure, 
  footer, header, hgroup, menu, nav, section {
    display: block;
  }
  body {
    line-height: 1;
  }
  ol, ul {
    list-style: none;
  }
  blockquote, q {
    quotes: none;
  }
  blockquote:before, blockquote:after,
  q:before, q:after {
    content: '';
    content: none;
  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  a { text-decoration: none; outline: none; }
  *, ::after, ::before {
      box-sizing: border-box;
  }
  a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, canvas, caption, center, cite, code, dd, del, details, dfn, dialog, div, dl, dt, em, embed, fieldset, figcaption, figure, font, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, html, i, iframe, img, ins, kbd, label, legend, li, main, mark, menu, meter, nav, object, ol, output, p, pre, progress, q, rp, rt, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video, xmp, input, input:focus, textarea, texrarea:focus, button, select {
    border: 0;
    margin: 0;
    padding: 0;
    font-size: 100%;
    outline: none !important;
    box-shadow: none;
    -webkit-appearance: none;
    border-radius: 0;
    background: transparent;
    border: 0;
    text-decoration: none !important;
    resize: none;
    -webkit-tap-highlight-color: transparent;
    tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-family: 'Roboto', serif !important;
  }
  *{
    -webkit-tap-highlight-color: transparent;
  }
  select{ cursor: pointer; }
  button, button:focus, button:hover{
    outline: none;
  }
  input,
  textarea,
  button {
    outline: none;
    /*font-family: 'Roboto', sans-serif;*/
  }

  img {
    max-width: 100%;
    font-style: italic;
    vertical-align: middle;
    image-rendering: -moz-crisp-edges; /* Firefox */
    image-rendering: -o-crisp-edges; /* Opera */
    image-rendering: -webkit-optimize-contrast; /* Webkit (non-standard naming) */
    image-rendering: crisp-edges;
    -ms-interpolation-mode: nearest-neighbor; /* IE (non-standard property) */
  }

  /*structural assets*/
    :root {
      --base_font_size: 1vw;
      --base_lineheight: 1vw;
      --base_margin: 1vw;
      --base_padding: 1vw;
      --base_number: 1vw;

      --body_color: #fff;
      
      --tc1: #F8F1E8;
      --tc2: #EDC47F;
      --tc3: #F0C0C1;
      --tc4: #74BDB1;
      --tc5: #DD6849;
      --tc6: #AEAEAC;
      --tcb: #000;
      --tcw: #fff;
      --fontColor: #000;
      --bgColor: #fff;
    }

    html,body{
      padding: 0;
      margin: 0;
      font-weight: 300;
      overflow-y: auto;
      /*height: 100%;*/
    }

    .theme-light{
      --fontColor: #000;
      --bgColor: #fff;
    }

    .theme-dark{
      --fontColor: #fff;
      --bgColor: #3d3d3d;
    }

    .theme-light .mode_icon_black{
      display: block;
    }

    .theme-light .mode_icon_white{
      display: none;    
    }

    .theme-dark .mode_icon_white{
      display: block;    
    }

    .theme-dark .mode_icon_black{
      display: none;    
    }

    /*::-webkit-scrollbar {
      display: none;
    }

    ::-moz-scrollbar {
      display: none;
    }

    ::-ms-scrollbar {
      display: none;
    }*/

    .clearfix::after {
      display: block;
      content: "";
      clear: both;
    }

    img[width],
    img[height] {
      max-width: none
    }

